.events_header {
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(255,170,0); //#274c5e
    height: 70px;
    h2 {
        color: white;
        font-size: 30px;
        letter-spacing: 1px;
        text-transform: uppercase;
    }
    .events_header_past {
        border-radius: 50%;
        width: 60px;
        height: 60px;
        background-color: white;
        border: 1px solid white;
        a {
            text-decoration: none;
            color: rgb(255,170,0);
            font-weight: 700;
            font-size: 20px;
        }
    }
}

.future-events {
    background: #d0d0d0;
    padding: 10px;
    .future-events_single {
        background-color: white;
        width: 80vw;
        height: 15vh;
        display: flex;
        justify-content: space-around;
        align-items: center;
        margin-bottom: 10px;
        .future-events_single_date {
            text-transform: uppercase;
            font-size: 18px;
        }
        .future-events_single_title {
            text-transform: uppercase;
            font-size: 18px;
            .bold {
                font-weight: 700;
            }
        }
        .future-events_single_place a {
            text-decoration: none;
            font-weight: 700;
            color: black;
            font-size: 18px;
        }
    }
}

.past-events {
    background-color: #d0d0d0;
}
